<script>
    export function getIco() {
        return "Текстовый блок";
    }

    export let item = [];

    export function getInitialData(id) {
        return {id: id, isActive: false, type: 'textAreaBlock', data: []};
    }

    export function getBlockType() {
        return "textAreaBlock";
    }

    function setStatus(status) { //для показа кнопки удаления
        setTimeout(function () {
            if (item) {
                item.isActive = status;
                item = item;
            }
        }, 100) //задержка для срабатывания удаления

    }

    function setData(e) {
        e.stopPropagation();
        e.preventDefault();
        item.data = e.target.innerHTML;
    }
</script>
<div class="textareaBlock" on:focus={()=>{setStatus(true)}} on:blur={()=>{setStatus(false)}} on:keyup={(e)=>setData(e)}
     contenteditable="true">{@html item.data}</div>
<style>
    .textareaBlock {
        width: 100%;
        min-width: 50px;
        min-height: 20px;
        float: left;
        border: 1px dashed gray;
        margin: 1px;

    }
</style>