<script>
    import Gen from "./generators/Gen";

    export let active;
    // export let isEditMode = false;
    export let row;
    export let numBlock = 0;
    export let rows = [];
    import Block from "./Block.svelte";
    import {beforeUpdate} from 'svelte';

    beforeUpdate(() => {
        //если кривая дата, обнулим ее чтобы ничего не ломалось
        if (typeof row.data !== "object") {
            row.data = [];
        }
        numBlock = row.data.length;
    });

    export function addBlock() {
        row.data.push({id: Gen.id(8), isActive: false, type: 'block', data: []});
        row = row;
    }
    export function delRow(row){
        let index = rows.indexOf(row);
        if (index > -1) { // only splice array when item is found
            rows.splice(index, 1); // 2nd parameter means remove one item only
        }
        rows = rows;
    }
</script>
<div on:click class="rowLine {active?'active':''}">
    {#if active && numBlock < 12}
        <div class="addBlock" on:click={()=>addBlock()}> +</div>
    {/if}
    {#if active}
        <div class="delBlock" on:click={()=>delRow(row)}> -</div>
    {/if}
    {#each row.data as item (item.id)}
        <Block bind:active="{item.isActive}" bind:row="{row}" bind:blockData="{item}"/>
    {/each}

</div>
<style>
    .rowLine {
        position: relative;
        cursor: pointer;
        display: flex;
        min-height: 25px;
        margin-top: 3px;
        outline: 1px dashed gray;
    }

    .active {
        background-color: #fafafa;
    }

    .addBlock {
        clear: both;
        right: 0;
        width: 25px;
        height: 25px;
        position: absolute;
        color: white;
        background-color: rebeccapurple;
        border-top-left-radius: 50%;
        border-bottom-left-radius: 50%;

        text-align: center;
        padding: 0;
        z-index: 2;
        cursor: pointer;
    }
    .delBlock {
        clear: both;
        right: 0;
        width: 25px;
        height: 25px;
        bottom: -25px;
        position: absolute;
        color: white;
        background-color: #a80606;
        border-bottom-left-radius: 50%;
        border-bottom-right-radius: 50%;

        text-align: center;
        padding: 0;
        z-index: 2;
        cursor: pointer;
    }


</style>
