<script>
    import Gen from "./generators/Gen";

    export let blockList = [];
    //Дата
    export let blockData = [];

    function addItem(e, obj) {
        e.stopPropagation();
        e.preventDefault();
        let dt = obj.prototype.getInitialData(Gen.id(8)); //id надо передать.... иначе svelte сходит с уа...
        blockData.push(dt);
        blockData = blockData;
    }

</script>
<div class="contentPanel">
    Добавляемый элемент
    <hr>
    {#each blockList as item}
        <button on:click="{(e)=>addItem(e,item)}">{item.prototype.getIco()}</button>
    {/each}
</div>
<style>
    .contentPanel {
        width: 250px;
        position: absolute;
        top: 0px;
        right: 0px;
        height: auto;
        min-height: 250px;
        margin-top: 25px;
        border: 2px solid #ebebeb;
        background-color: #fdfdfd;
        z-index: 50;
        color: black;
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;

    }

</style>