<script>
    import ContentPanel from "./ContentPanel.svelte";
    import textAreaBlock from "./blocks/textAreaBlock.svelte";
    import codeBlock from "./blocks/codeBlock.svelte";


    let blockList = [];
    blockList.push(textAreaBlock);
    blockList.push(codeBlock)

    export let active = false;
    export let activeAddWindow = false;
    export let blockData = [];
    let activeBlock = null;
    export let row = [];



    export function toggleActive(e) {
        if (e.target.classList.contains('block')) {
            active = !active;
        }
    }

    export function addToActiveBlock(e) {
        e.stopPropagation();
        e.preventDefault();
        activeAddWindow = !activeAddWindow;
    }
    export function stopPropagation(e) {
        e.stopPropagation();
        e.preventDefault();

    }
    function removeBlock(item) {
        let index = row.data.indexOf(item);
        if (index > -1) { // only splice array when item is found
            row.data.splice(index, 1); // 2nd parameter means remove one item only
        }
        row = row;
    }

    function removeElement(item) {
        setTimeout(function () {
            let index = blockData.data.indexOf(item);
            if (index > -1) { // only splice array when item is found
                blockData.data.splice(index, 1); // 2nd parameter means remove one item only
            }
            blockData = blockData;
        }, 120);

    }

</script>
<div on:click={(e)=>toggleActive(e)} class="{active?'block active':'block'}">
    {#each blockData.data as item (item.id)}
        <!--{item.id}-->
        {#each blockList as block}
            {#if (item.type === block.prototype.getBlockType())}
                <div class="blockElement">
                    <svelte:component bind:item={item} this="{block}"/>
                    {#if item.isActive}
                        <div class="delElement" on:click={()=>removeElement(item)}>-</div>
                    {/if}
                </div>
            {/if}
        {/each}

    {/each}
    {#if active}
        <div class="addBlock" on:click={(e)=>addToActiveBlock(e)}>
            {#if activeAddWindow} &and;{:else}&or;{/if}
            {#if activeAddWindow}
                <ContentPanel blockList={blockList} on:click={(e)=>stopPropagation(e)} bind:blockData={blockData.data}/>
            {/if}
        </div>
        <div class="delBlock" on:click={removeBlock(blockData)}>
            -
        </div>

    {/if}
</div>
<style>
    .addBlock {
        clear: both;
        width: 25px;
        height: 25px;
        position: absolute;
        right: 0px;
        bottom: -25px;
        color: white;
        background-color: rebeccapurple;
        border-bottom-left-radius: 50%;
        border-bottom-right-radius: 50%;
        text-align: center;
        padding: 0px;
        z-index: 2;
        cursor: pointer;

    }

    .delBlock {
        display: block;
        width: 20px;
        height: 17px;
        background-color: #a80606;
        color: white;
        text-align: center;
        padding: 3px;
        position: absolute;
        top: 0px;
        right: 0px;
        border-bottom-left-radius: 50%;
        line-height: 10px;
    }


    .blockElement {
        position: relative;
        flex-grow: 1;
    }

    .active {
        background-color: #f8f7ef;
    }

    .delElement {

        /* color: green; */
        display: block;
        width: 20px;
        height: 17px;
        background-color: #a80606;
        color: white;
        text-align: center;
        padding: 3px;
        position: absolute;
        bottom: 0px;
        top: 0px;
        right: 0px;
        border-bottom-left-radius: 50%;
        line-height: 10px;

    }

    .block {
        display: flex;
        min-height: 25px;
        position: relative;
        margin: 5px;
        padding: 5px;
        outline: 1px dashed gray;
        flex-grow: 1;
    }
</style>