<script>
    export function getIco() {
        return "блок с кодом";
    }

    export let item = [];

    export function getInitialData(id) {
        let res = {id: id, isActive: false, type: 'codeBlock', data: []}
        return res;
    }

    export function getBlockType() {
        return "codeBlock";
    }

    function setStatus(status) { //для показа кнопки удаления
        setTimeout(function () {
            if (item) {
                item.isActive = status;
                item = item;
            }
        }, 100) //задержка для срабатывания удаления

    }

    function setData(e) {
        e.stopPropagation();
        e.preventDefault();
        item.data = e.target.value;

    }
</script>

<textarea class="textareaBlock"
          on:focus={()=>{setStatus(true)}}
          on:blur={()=>{setStatus(false)}}
          on:change={(e)=>setData(e)}>{item.data}</textarea>
<style>
    .textareaBlock {
        width: 100%;
        min-height: 20px;
        float: left;
        border: 1px dashed gray;
        margin: 1px;

    }

</style>