<script>
    import Row from "./row.svelte";
    // import {beforeUpdate} from 'svelte';
    import Gen from "./generators/Gen";

    // beforeUpdate(() => {
    //     console.log('the component just updated');
    //     console.log(objects);
    //
    //
    // });
    let RowActiveId = null;

    let objects = [
        {
            type: 'row', id: Gen.id(8), isActive: false, data: []
        }];

    export function getData() {
        return objects;
    }

    export function setData(data) {
        objects = data;
    }

    export function addRow(e = false) {
        e.stopPropagation();
        e.preventDefault();
        objects.push({id: Gen.id(8), isActive: false, type: 'row', data: []});
        objects = objects;

    }

    function disableElements(objects, itemId) {
        objects.forEach((item) => {
            if (item.id === itemId) {
                item.isActive = !item.isActive;
            } else {
                item.isActive = false;
            }
            if (typeof item.data === "object") {
                disableElements(item.data);
            }
        });
        return objects;
    }

    function toggleElement(e, itemId) {

        if (!e.target.classList.contains('rowLine')) {
            // убрал ререндер при повторном клике
            return
        }

        RowActiveId = itemId;
        objects = disableElements(objects, itemId);

    }

</script>
<div>

    {#each objects as item (item.id)}
        {#if (item.type == 'row')}
            <Row on:click="{(e)=>toggleElement(e,item.id)}" bind:row="{item}" bind:rows={objects}
                 active="{item.isActive}"></Row>
        {/if}
    {/each}
    <div class="addBlock" on:click={(e)=>addRow(e)}> +</div>

</div>
<style>
    .addBlock {
        clear: both;
        left: calc(50% - 12px);
        width: 25px;
        height: 25px;
        position: absolute;
        right: 0px;
        color: white;
        background-color: rebeccapurple;
        border-bottom-left-radius: 50%;
        border-bottom-right-radius: 50%;
        text-align: center;
        padding: 0px;
        cursor: pointer;
    }
</style>
